:root {
  --redhatdisplay: "Red Hat Display", sans-serif;
  --lexend-deca: "Lexend Deca", sans-serif;
  --inter: "Inter", sans-serif;
}

:root {
  --light: #fff;
  --dark: #000;
  --very-dark-blue: hsl(233, 47%, 7%);
  --dark-desaturated-blue: hsl(244, 38%, 16%);
  --soft-violet: hsl(277, 64%, 61%);
  --white: hsl(0, 0%, 100%);
  --slightly-transparent-white-1: hsla(0, 0%, 100%, 0.75);
  --slightly-transparent-white-2: hsla(0, 0%, 100%, 0.6);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}
body .main {
  background-color: var(--very-dark-blue);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
body .attribution {
  font-size: 11px;
  text-align: center;
}
body .attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  height: 60%;
  width: 80%;
  justify-content: center;
  align-items: center;
  margin: 10.9375rem auto;
  display: flex;
  border-radius: 0.9375rem;
  background-color: var(--dark-desaturated-blue);
}
@media (max-width: 39.9375em) {
  .card {
    height: 90%;
    width: 60%;
    flex-direction: column-reverse;
  }
}
.card__summary {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 5rem 7.5rem 4.0625rem 4.375rem;
  letter-spacing: 0.06875rem;
  line-height: 1.5;
}
@media (max-width: 39.9375em) {
  .card__summary {
    padding: 2.5rem 1.875rem 2.5rem 1.875rem;
  }
}
.card__summary__info__headline h1 {
  font-size: 2rem;
  color: var(--white);
  font-family: var(--lexend-deca);
}
@media (max-width: 39.9375em) {
  .card__summary__info__headline h1 {
    text-align: center;
    font-size: 1.5rem;
  }
}
.card__summary__info__headline h1 span {
  color: var(--soft-violet);
}
.card__summary__info__tagline p {
  font-size: 0.875rem;
  margin-top: 2.1875rem;
  color: var(--slightly-transparent-white-1);
  font-family: var(--inter);
}
@media (max-width: 39.9375em) {
  .card__summary__info__tagline p {
    font-size: 0.8125rem;
    text-align: center;
  }
}
.card__summary__stats {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
}
@media (max-width: 39.9375em) {
  .card__summary__stats {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 1.25rem;
  }
}
@media (max-width: 39.9375em) {
  .card__summary__stats div {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 1.25rem;
  }
}
.card__summary__stats div .value {
  font-size: 1.5625rem;
  color: var(--white);
  font-family: var(--lexend-deca);
  text-transform: uppercase;
}
.card__summary__stats div .label {
  text-transform: uppercase;
  font-size: 0.625rem;
  color: var(--slightly-transparent-white-2);
  font-family: var(--inter);
}
.card__image {
  border-radius: 0rem 0.9375rem 0.9375rem 0rem;
  max-height: 27.875rem;
  max-width: 33.75rem;
  background: var(--soft-violet);
}
@media (max-width: 39.9375em) {
  .card__image {
    border-radius: 0.9375rem 0.9375rem 0rem 0rem;
  }
}
.card__image img {
  border-radius: 0rem 0.9375rem 0.9375rem 0rem;
  max-height: 27.875rem;
  max-width: 33.75rem;
  height: 100%;
  opacity: 0.4;
}
@media (max-width: 39.9375em) {
  .card__image img {
    height: 100%;
    width: 100%;
    flex-direction: column-reverse;
    border-radius: 0.9375rem 0.9375rem 0rem 0rem;
  }
}